import * as React from "react";
import HeroImageRef from "../../images/backdrop/hero-image.jpg";
import "./HeroSection.scss";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faFacebookSquare, faInstagramSquare, faTwitterSquare } from '@fortawesome/free-brands-svg-icons'

export const HeroSection = () => {
    return (
        <div className="hero-section">
            <div className="hero-section-background" style={
                {
                    backgroundImage: `linear-gradient(
                        rgba(0, 0, 0, 0),
                        rgba(0, 0, 0, 0),
                        rgba(0, 0, 0, 0.25),
                        rgba(0, 0, 0, 0.75), 
                        rgba(0, 0, 0, 1)
                      ),
                      url(${HeroImageRef})`
                }
            }>
                <div className="flex-column hero-section-title-wrapper">
                    <div className="hero-section-game-title">
                        <div className="hero-section-codename">Codename:</div>
                        <div className="red-text">Apocalypse</div>
                    </div>
                    <div className="hero-section-game-desc">
                        <span>A roguelike deckbuilder featuring a pre-</span>
                        <span className="red-text"><b>apocalypse </b></span>
                        <span>narrative experience</span>
                    </div>
                    <div style={{ height: "120px" }}></div>
                </div>
                <div className="hero-section-social flex-column">
                    <div className="hero-section-coming-soon">
                        COMING SOON... <span className="red-text">Q2 2022</span>
                    </div>
                    <div className="hero-section-coming-soon-desc">
                        Follow us on social media platforms to receive latest news!
                    </div>
                    <div className="hero-section-social-media-container flex-row">
                        <a href="https://www.facebook.com/profile.php?id=100075844770182" target="_blank">
                            <FontAwesomeIcon className="hero-section-social-media-icon" icon={faFacebookSquare} color="white" size="2x" />
                        </a>
                        <a href="https://www.instagram.com/codename_apocalypse/" target="_blank">
                            <FontAwesomeIcon className="hero-section-social-media-icon" icon={faInstagramSquare} color="white" size="2x" />
                        </a>
                        <a href="https://twitter.com/RankB_Dev" target="_blank">
                            <FontAwesomeIcon className="hero-section-social-media-icon" icon={faTwitterSquare} color="white" size="2x" />
                        </a>
                    </div>
                    <div className="hero-section-cr">
                        Copyright © RankB ・ Powered by Boreas He ・ 2022 ・ All Rights Reserved
                    </div>
                </div>
            </div>
        </div>
    )
}
